body {
    font-family: $main-font, sans-serif;
    font-size: 16px;
}
.wrapper {
    position: relative;
    z-index: 10;
    overflow: hidden;
}
.container {
    margin: 0 auto;
    padding: 0 20px;
    max-width: 1240px;
    width: 100%;
    overflow: hidden;
}
p {
    margin-bottom: 50px;
}
.pseudo-hidden {
    position: absolute;
    left: -200vw;
    opacity: 0;
    z-index: -999;
    pointer-events: none;
    overflow: hidden;
    transform: scale(0);
}
.irs-single {
    background-color: #E31E24 !important;
    font-weight: 700;
    &:before {
        border-top-color: #E31E24 !important;
    }
}
.irs-handle.single {
    border-color: #E31E24 !important;
}
.irs--round .irs-bar {
    background-color: #E31E24 !important
}


